<template>
  <div class="rts-container pa-4">
    <div class="text-h6 mb-4">規模報酬分析</div>
    <div class="mb-5 text-left">
      將相對有效率線體連線，形成效率前緣包絡線；而相對無效率線體則會被包絡線包圍
    </div>
    <div id="rts" />
  </div>
</template>

<script>
/* eslint-disable */
import * as d3 from "d3";
import axios from "axios";
export default {
  name: "RTS",
  mounted() {
    // Initialize the chart
    // this.getDeaData();
    //  window.addEventListener("resize", this.reDraw);
    d3.select(window).on("resize", this.getDea);
    this.getDea();
  },
  data() {
    return {
      RTSdata: [
        {
          name: "origin",
          CRS: 1.0,
          u0: -0.625,
          X: 0,
          Y: 0,
        },
        {
          name: "S1",
          CRS: 1.0,
          VRS: 1.0,
          u0: -0.625,
          X: 20.1,
          Y: 2061.2,
        },
        {
          name: "S2",
          CRS: 0.357,
          VRS: 1.0,
          u0: -1.0,
          X: 14.5,
          Y: 531.2,
        },
        {
          name: "S3",
          CRS: 0.976,
          VRS: 1.0,
          u0: 0.275,
          X: 22.4,
          Y: 2241.7,
        },
        {
          name: "S5",
          CRS: 0.291,
          VRS: 0.699,
          u0: -0.589,
          X: 21.3,
          Y: 635.3,
        },
        {
          name: "S6",
          CRS: 0.183,
          VRS: 0.63,
          u0: -0.63,
          X: 23.0,
          Y: 430.7,
        },
      ],
    };
  },
  methods: {
    getDeaData() {
      const self = this;
      axios
        .get("http://10.61.128.31:8100/DEA/AE")
        .then((response) => {
          if (response.data) {
            this.getDea(response.data);
            // this.reDraw();

            // Add an event listener that run the function when dimension change
          }
        })
        .catch(function (error) {
          // 请求失败处理
          console.log(error);
        });
    },
    getDea() {
      console.log("testdeaaa");
      d3.select("#rts").select("svg").remove();
      const data = this.RTSdata;
      //   data.sort(function (a, b) {
      //     return Number(b.DMU) - Number(a.DMU) || b.y - a.y;
      //   });

      let chart = this.createGraph();
      d3.select("#rts").datum(data).call(chart);
    },
    createGraph() {
      const self = this;
      const containerWidth = parseInt(
        d3.select(".rts-container").style("width"),
        10
      );
      const containerHeight =
        containerWidth > 1024 ? 500 : containerWidth * 0.625;
      const circleSize = containerWidth > 1024 ? 10 : 5;
      const textSize = containerWidth > 1024 ? "18px" : "12px";
      let margin = { top: 20, left: 40, right: 20, bottom: 30 };
      let options = {
        height: containerHeight - margin.top - margin.bottom,
        width: containerWidth - margin.left * 3,
      };

      function chart(selection) {
        selection.each(function (data) {
          console.log(data);
          let maxXaxis = d3.max(data, function (c) {
              //   if (c.name !== "origin") {
              return c.X;
              //   }
            }),
            maxYaxis = d3.max(data, function (c) {
              //   if (c.name !== "origin") {
              return c.Y;
              //   }
            });

          let x = d3
            .scaleLinear()
            .domain([0, maxXaxis * 1.5])
            .range([0, options.width]);

          let y = d3
            .scaleLinear()
            .domain([0, maxYaxis * 1.2])
            .range([options.height, 0]);

          let svg = d3
            .select("#rts")
            .append("svg")
            .attr("width", options.width + margin.right + margin.left)
            .attr("height", options.height + margin.top + margin.bottom);

          svg
            .append("rect")
            .attr("width", "100%")
            .attr("height", "100%")
            .attr("fill", "rgba(0, 0, 0, 0.2)");

          // self.svg = d3
          //   .select("#dea")
          //   .append("svg")
          //   .attr("width", options.width + margin.right + margin.left)
          //   .attr("height", options.height + margin.top + margin.bottom);
          let main = svg
            .append("g")
            .attr(
              "transform",
              "translate(" + margin.left + "," + margin.top + ")"
            )
            .attr("width", options.width)
            .attr("height", options.height);

          //   // draw the x axis
          let xAxis = d3.axisBottom().scale(x).ticks(5);
          // self.xAxis = d3.axisBottom().scale(self.x).ticks(5);

          main
            .append("g")
            .attr("transform", "translate(0," + options.height + ")")
            // .attr("class", "main axis date")
            .attr("stroke", "white")
            .call(xAxis)
            .append("text")
            .attr("y", -13)
            .attr("x", options.width)
            .attr("dy", ".71em")
            .style("text-anchor", "end")
            .text("良品產出數");

          // // draw the y axis
          let yAxis = d3.axisLeft().scale(y).ticks(10);

          main
            .append("g")
            // .attr("transform", "translate(0,0)")
            // .attr("class", "main axis date")
            .attr("stroke", "white")
            .call(yAxis)
            .append("text")
            .attr("transform", "rotate(-90)")
            .attr("y", 6)
            .attr("dy", ".71em")
            .style("text-anchor", "end")
            .text("直接人力數");

          let g = main.append("g");

          g.selectAll("circle")
            .data(data)
            .enter()
            .append("circle")
            .attr("cx", function (d, i) {
              d.svgX = x(d.X);
              return x(d.X);
            })
            .attr("cy", function (d) {
              d.svgY = y(d.Y);
              return y(d.Y);
            })
            .attr("r", circleSize)
            .attr("class", "circle");

          // 畫線
          //   let line = d3.svg
          let line = d3
            .line() // 定義線段
            .x(function (d) {
              console.log("x", x(d.X));
              return x(d.X);
            })
            .y(function (d) {
              return y(d.Y);
            })
            .defined(function (d) {
              return d.CRS == 1.0;
            });

          g.append("path")
            .attr("d", line(data)) // 使用定義線段
            .attr("stroke", "#AE0000")
            .attr("stroke-width", 2)
            .attr("fill", "none");

          const mainCRS = data.find(
            (item) => item.CRS == 1 && item.name !== "origin"
          );
          console.log(mainCRS.svgX, mainCRS.svgY);

          data.forEach((item) => {
            if (item.VRS == 1 && item.CRS !== 1) {
              let path = d3.path();
              path.moveTo(mainCRS.svgX, mainCRS.svgY);
              path.lineTo(item.svgX, item.svgY);

              g.append("path")
                .attr("d", path) // 使用定義線段
                .attr("stroke", "#AE0000")
                .attr("stroke-width", 2)
                .attr("fill", "none");
            }
          });

          g.selectAll("text")
            .data(data)
            .enter()
            .append("text")
            .attr("x", function (d, i) {
              return x(d.X);
            })
            .attr("y", function (d, i) {
              return y(d.Y);
            })
            .attr("dy", "1em")
            .attr("style", `font-size: ${textSize};fill: white`)
            .text(function (d) {
              if (d.name !== "origin") {
                return d.name;
              }
            });
        });
      }

      return chart;
    },
    reDraw() {
      console.log("test11111");
      // get the current width of the div where the chart appear, and attribute it to Svg
      let currentWidth = parseInt(d3.select("#dea").style("width"), 10);
      console.log(this.svg);
      this.svg.attr("width", currentWidth);

      // // Update the X scale and Axis (here the 20 is just to have a bit of margin)

      this.x.range([20, currentWidth - 20]);
      this.xAxis.call(d3.axisBottom(this.x));

      // // Add the last information needed for the circles: their X position
      // myCircles.attr("cx", function (d) {
      //   return x(d);
      // });
    },
  },
};
</script>

