<template>
  <div class="dea-container pa-4">
    <div class="text-h6 mb-4">效益評比分析</div>
    <div class="mb-5 text-left">
      將相對有效率線體連線，形成效率前緣包絡線；而相對無效率線體則會被包絡線包圍
    </div>
    <div id="dea" />
  </div>
</template>

<script>
/* eslint-disable */
import * as d3 from "d3";
import axios from "axios";
export default {
  name: "DEA",
  mounted() {
    // Initialize the chart
    // this.getDeaData();
    //  window.addEventListener("resize", this.reDraw);
    d3.select(window).on('resize', this.getDea);
    this.getDea();
  },
  data() {
    return {
      svg: "",
      x: "",
      xAxis: "",
      AEdata: [
        {
          name: "S03",
          X: 0.029931358381502886,
          Y: 0.027981260982445872,
          DMU: 1.0,
          X_name: "AVL/DL",
          Y_name: "yield/DL",
          DMU_new: 1.0,
        },
        {
          name: "S06",
          X: 0.015882967607105537,
          Y: 0.007321415834655656,
          DMU: 1.0,
          X_name: "AVL/DL",
          Y_name: "yield/DL",
          DMU_new: 1.0,
        },
        {
          name: "S02",
          X: 0.024839262751821693,
          Y: 0.031692624407748636,
          DMU: 0.9938893481974862,
          X_name: "AVL/DL",
          Y_name: "yield/DL",
          DMU_new: 1.0,
        },
        {
          name: "S01",
          X: 0.027351916376306618,
          Y: 0.028444969692314855,
          DMU: 0.9938204883779975,
          X_name: "AVL/DL",
          Y_name: "yield/DL",
        },
        {
          name: "S05",
          X: 0.021759549154665,
          Y: 0.032176289109146675,
          DMU: 0.9210628914438008,
          X_name: "AVL/DL",
          Y_name: "yield/DL",
        },
      ],
    };
  },
  methods: {
    getDeaData() {
      const self = this;
      axios
        .get("http://10.61.128.31:8100/DEA/AE")
        .then((response) => {
          if (response.data) {
            this.getDea(response.data);
            // this.reDraw();

            // Add an event listener that run the function when dimension change
          }
        })
        .catch(function (error) {
          // 请求失败处理
          console.log(error);
        });
    },
    getDea() {
      console.log('testdeaaa');
      d3.select("#dea").select("svg").remove();
      const data = this.AEdata
      data.sort(function (a, b) {
        return Number(b.DMU) - Number(a.DMU) || b.y - a.y;
      });

      let chart = this.createGraph();
      d3.select("#dea").datum(data).call(chart);
    },
    createGraph() {
      const self = this;
      const containerWidth = parseInt(d3.select(".dea-container").style("width"), 10)
      const containerHeight = (containerWidth > 1024) ? 500 : containerWidth * 0.625
      const circleSize = (containerWidth > 1024) ? 10 : 5
      const textSize = (containerWidth > 1024) ? '18px' : '12px'
      let margin = { top: 20, left: 40, right: 20, bottom: 30 };
      let options = {
        height: containerHeight - margin.top - margin.bottom,
        width: containerWidth - (margin.left * 3),
      };
      
      function chart(selection) {
        selection.each(function (data) {
          let maxXaxis = d3.max(data, function (c) {
              return c.X;
            }),
            minXaxis = d3.min(data, function (c) {
              return c.X;
            }),
            maxYaxis = d3.max(data, function (c) {
              return c.Y;
            }),
            minYaxis = d3.min(data, function (c) {
              return c.Y;
            });

          let x = d3
            .scaleLog()
            .domain([minXaxis, maxXaxis * 1.2])
            .range([0, options.width]);

          // self.x = d3
          //   .scaleLog()
          //   .domain([minXaxis, maxXaxis * 1.2])
          //   .range([0, options.width]);

          let y = d3
            .scaleLinear()
            .domain([0, maxYaxis])
            .range([options.height, 0]);

          let svg = d3
            .select("#dea")
            .append("svg")
            .attr("width", options.width + margin.right + margin.left)
            .attr("height", options.height + margin.top + margin.bottom);

          svg
            .append("rect")
            .attr("width", "100%")
            .attr("height", "100%")
            .attr("fill", "rgba(0, 0, 0, 0.2)");

          // self.svg = d3
          //   .select("#dea")
          //   .append("svg")
          //   .attr("width", options.width + margin.right + margin.left)
          //   .attr("height", options.height + margin.top + margin.bottom);
          let main = svg
            .append("g")
            .attr(
              "transform",
              "translate(" + margin.left + "," + margin.top + ")"
            )
            .attr("width", options.width)
            .attr("height", options.height);

          //   // draw the x axis
          let xAxis = d3.axisBottom().scale(x).ticks(5);
          // self.xAxis = d3.axisBottom().scale(self.x).ticks(5);

          main
            .append("g")
            .attr("transform", "translate(0," + options.height + ")")
            // .attr("class", "main axis date")
            .attr("stroke", "white")
            .call(xAxis)
            .append("text")
            .attr("y", -13)
            .attr("x", options.width)
            .attr("dy", ".71em")
            .style("text-anchor", "end")
            .text("稼動率／直接人力數");

          // // draw the y axis
          let yAxis = d3.axisLeft().scale(y).ticks(10);

          main
            .append("g")
            // .attr("transform", "translate(0,0)")
            // .attr("class", "main axis date")
            .attr("stroke", "white")
            .call(yAxis)
            .append("text")
            .attr("transform", "rotate(-90)")
            .attr("y", 6)
            .attr("dy", ".71em")
            .style("text-anchor", "end")
            .text("良率/直接人力數");

          let g = main.append("g");

          g.selectAll("circle")
            .data(data)
            .enter()
            .append("circle")
            .attr("cx", function (d, i) {
              return x(d.X);
            })
            .attr("cy", function (d) {
              return y(d.Y);
            })
            .attr("r", circleSize)
            .attr("class", "circle");

          // 畫線
          // let line = d3.svg
          let line = d3
            .line() // 定義線段
            .x(function (d) {
              console.log(d);
              return x(d.X);
            })
            .y(function (d) {
              return y(d.Y);
            })
            .curve(d3.curveNatural)
            .defined(function (d) {
              return d.DMU_new == 1;
            });

          g.append("path")
            .attr("d", line(data)) // 使用定義線段
            .attr("stroke", "#AE0000")
            .attr("stroke-width", 2)
            .attr("fill", "none");

          g.selectAll("text")
            .data(data)
            .enter()
            .append("text")
            .attr("x", function (d, i) {
              return x(d.X);
            })
            .attr("y", function (d, i) {
              return y(d.Y);
            })
            .attr("dy", "1em")
            .attr("style", `font-size: ${textSize};fill: white`)
            .text(function (d) {
              return d.name;
            });
        });
      }

      return chart;
    },
    reDraw() {
      console.log("test11111");
      // get the current width of the div where the chart appear, and attribute it to Svg
      let currentWidth = parseInt(d3.select("#dea").style("width"), 10);
      console.log(this.svg);
      this.svg.attr("width", currentWidth);

      // // Update the X scale and Axis (here the 20 is just to have a bit of margin)

      this.x.range([20, currentWidth - 20]);
      this.xAxis.call(d3.axisBottom(this.x));

      // // Add the last information needed for the circles: their X position
      // myCircles.attr("cx", function (d) {
      //   return x(d);
      // });
    },
  },
};
</script>

