  color: #ffffff;
  background-color: #29303d;
  border-radius: 10px;
  width: 100%;
  min-width: 300px;
  // height: 100%;
  // max-width: 960px;
  // max-height: 500px;
  overflow: hidden;
}
.circle {
  fill: #4098af;
}
