import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
  },
  mutations: {
    setBreakPoint(state, width) {
      switch (true) {
        case width > 1904: {
          state.breakpoint = "xl";
          break;
        }
        case width > 1264 && width <= 1904: {
          state.breakpoint = "lg";
          break;
        }
        case width > 960 && width <= 1264: {
          state.breakpoint = "md";
          break;
        }
        case width > 600 && width <= 960: {
          state.breakpoint = "sm";
          break;
        }
        case width <= 600: {
          state.breakpoint = "xs";
          break;
        }
      }
    }
  },
  actions: {
  },
  modules: {
  }
})