<template>
  <div class="tornado-container">
    <div class="text-h6 py-4">敏感度分析</div>
    <div class="mb-3 text-left px-2">
      龍捲風圖是敏感度分析常用呈現方式，依各屬性變化由大至小依序排列，
      因改善成本和資源不同，從目標產出及屬性因子角度預測整體變化幅度，提供改善者不同改善方向。
    </div>
    <div id="tornado" />
  </div>
</template>

<script>
/* eslint-disable */
import * as d3 from "d3";
export default {
  name: "Tornado",
  mounted() {
    d3.select(window).on('resize', this.getTornado);
    this.getTornado();
  },
  methods: {
    getTornado() {
      console.log('ttttttt');
      d3.select("#tornado").select("svg").remove();
      const data = [
        { type: "AVL", interactions: 0.053857210062966177 },
        { type: "TR", interactions: -0.017697526471705638 },
        { type: "DL", interactions: 0.010566802497212757 },
        { type: "yield", interactions: 0.002579102832038549 },
        { type: "AVL", interactions: -0.053857210062966177 },
        { type: "TR", interactions: 0.017697526471705638 },
        { type: "DL", interactions: -0.010566802497212757 },
        { type: "yield", interactions: -0.002579102832038549 },
      ];
      let chart = this.dornadoChart();
      d3.select("#tornado").datum(data).call(chart);
    },
    dornadoChart() {
      let margin = { top: 20, left: 40, right: 20, bottom: 30 }
        

      const containerWidth = parseInt(d3.select(".tornado-container").style("width"),10)
      const containerHeight = containerWidth > 1024 ? 200 : containerWidth * 0.625

      let width = containerWidth - (margin.left * 3)
      let height = containerHeight - margin.top - margin.bottom

      let x = d3.scaleLinear().range([0, width]);

      // let y = d3.scale.ordinal().rangeRoundBands([0, height], 0.1);
      let y = d3.scaleBand().rangeRound([0, height]).padding(0.1);

      // let xAxis = d3.svg.axis().scale(x).orient("bottom").ticks(7);
      let xAxis = d3.axisBottom().scale(x).ticks(7);

      // let yAxis = d3.svg.axis().scale(y).orient("left").tickSize(0);
      let yAxis = d3.axisLeft().scale(y).tickSize(0);
      let svg = d3
        .select("#tornado")
        .append("svg")
        .attr("width", width + margin.left + margin.right)
        .attr("height", height + margin.top + margin.bottom)
        .append("g")
        .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

      function chart(selection) {
        selection.each(function (data) {
          x.domain(
            d3.extent(data, function (d) {
              return d.interactions;
            })
          ).nice();
          y.domain(
            data.map(function (d) {
              return d.type;
            })
          );

          let minInteractions = Math.min.apply(
            Math,
            data.map(function (o) {
              return o.interactions;
            })
          );
          yAxis.tickPadding(Math.abs(x(minInteractions) - x(0)) + 10);

          let bar = svg.selectAll(".bar").data(data);

          bar
            .enter()
            .append("rect")
            .attr("class", function (d) {
              return (
                "bar bar--" + (d.interactions < 0 ? "negative" : "positive")
              );
            })
            .attr("x", function (d) {
              return x(Math.min(0, d.interactions));
            })
            .attr("y", function (d) {
              return y(d.type);
            })
            .attr("width", function (d) {
              return Math.abs(x(d.interactions) - x(0));
            })
            .attr("height", y.bandwidth());

          // bar
          //   .enter()
          //   .append("text")
          //   .attr("text-anchor", "middle")
          //   .attr("x", function (d, i) {
          //     return (
          //       x(Math.min(0, d.interactions)) +
          //       Math.abs(x(d.interactions) - x(0)) / 2
          //     );
          //   })
          //   .attr("y", function (d, i) {
          //     return y(d.type) + y.bandwidth() / 2;
          //   })
          //   .attr("dy", ".35em")
          //   .text(function (d) {
          //     return `${d.interactions.toFixed(3)}%`;
          //   });

          svg
            .append("g")
            .attr("class", "x axis")
            .attr("transform", "translate(0," + height + ")")
            .call(xAxis);

          svg
            .append("g")
            .attr("class", "y axis")
            .attr("transform", "translate(" + x(0) + ",0)")
            .call(yAxis);
        });
      }
      return chart;
    },
  },
};
</script>

